.icon {
  padding-top: 30px;
  padding-bottom: 31px;
    @media screen and (min-width:768px){
      padding-top: 40px;
      padding-bottom: 19px;
    }
  }
  
  .icon-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 18px;
     @media screen and (min-width:768px){
      display: flex;
      flex-direction: row;
      justify-content: center;
      gap: 51px;
    }
    @media screen and (min-width:1360px) {
      display: flex;
      flex-direction: row;
      justify-content: center;
      gap: 71px;
    }
  }
  
  .icon-item {
    text-align: center;
  }
  
  .icon-box {
    position: relative;
  
    display: inline-block;
    width: 67px;
    height: 67px;
    margin-bottom: 15px;
    border-radius: 50%;
  
    background-color: var(--secondary-green-color);
    fill: var(--main-wight-color);
  }
  
  .icon-svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .icon-title {
    margin-bottom: 16px;
  
    font-weight: 400;
    font-size: 20px;
    line-height: calc(33 / 20);
  }
  
  .icon-text {
    width: 220px;
    font-weight: 400;
    font-size: 16px;
    line-height: calc(26/16);
    @media screen and (min-width: 768px) {
      width: 185px;
      font-size: 18px;
      line-height: calc(28/18);
    }
    @media screen and (min-width: 1360px){
      width: 362px;
      font-size: 18px;
      line-height: 1.55;
    }
  }