  :root {
    --main-wight-color: #ffffff;
    --main-text-color: #333333;
    --hero-text-color: #e9e9e9;
    --secondary-green-color: #28a745;
    --first-bg-color: #f4f4f4;
    --secondary-bg-color: #0284d0;
    --form-text-color: #808080;
    --gradient-hero: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4));
  }
  
  body {
    font-family: 'Open Sans', sans-serif;
    color: var(--main-text-color);
  
    background-color: var(--main-wight-color);
    color: var(--main-text-color);
    font-size: 14px;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  ul {
    margin: 0;
    padding: 0;
  }
  
  .list {
    list-style: none;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  .link {
    text-decoration: none;
  }
  .container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @media screen and (min-width:320px){
      width: 320px;
    }
    @media screen and (min-width:768px) {
      width: 768px;
    }
    @media screen and (min-width:1360px) {
      width: 1360px;
    }
}
